.toggle-theme {
    cursor: "pointer";
    height: 16px;
    width: 16px;
  }
  
  .nav-theme {
    background-color: #101018f8;
    font-size: 20px;
    font-family: Comic Sans MS, Comic Sans, cursive;
  }
  
  .animate-navbar {
    box-shadow: 1px 1px 1px #222;
    animation: moveDown 0.5s ease-in-out;
  }
  
  @keyframes moveDown {
    from {
      transform: translateY(-5rem);
    }
    to {
      transform: translateY(0rem);
    }
  }